import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: () => import('@/views/home/index'),
    },
    {
      path: '/addBankcard',
      name: 'addBankcard',
      component: () => import('@/views/home/addBankcard'),
    },//consult
    {
      path: '/consult',
      name: 'consult',
      component: () => import('@/views/home/consult'),
    },//consult
    // {//首页
    //   path: '/',
    //   name:'index',
    //   component:()=>import('@views/home/index'),
    // },
    // {//添加银行卡
    //   path: '/addBankcard',
    //   name:'addBankcard',
    //   component:()=>import('@views/home/addBankcard'),
    // },
    //addBankcard
  ]
})



